.app {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
@media (max-width: 1418px) {
  .left{
    display: none;
  }
}
@media (max-width: 1040px) {
  .right{
    display: none;
  }
}
.app-stories {
  margin-bottom: 40px;
  border-radius: 7px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px,
    rgb(209, 213, 219) 0px 0px 0px 1px inset;
  background-color: white;
}
.app-stories-container {
  overflow: auto;
  display: flex;
}
.app-suggestions {
  border-radius: 7px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px,
    rgb(209, 213, 219) 0px 0px 0px 1px inset;
  background-color: white;
  overflow: auto;
  height: 300px;
}
.app-posts {
}
.component-container {
  width: 600px;
  margin: 40px 20px;
}
@media (max-width: 625px) {
  .component-container {
    width: 100%;
  }
}
.component-container__sm {
  width: 350px;
  margin: 40px 20px;
}
.side-bars {
  margin: 40px auto;
  width: 400px;
  height: 350px;
  background-color: white;
  border-radius: 7px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px,
    rgb(209, 213, 219) 0px 0px 0px 1px inset;
  overflow: auto;
}
